@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: "Inter", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: "Inter", sans-serif;
}

.Mui-selected {
  background-color: #ececec;
}
.Mui-selected .tab-icon {
  color: #000000;
}
.Mui-selected .collapsedtab-icon {
  color: #000000;
}
.Mui-selected .tab-text {
  color: #000000;
}

@layer components {
  .tab-icon {
    @apply relative flex items-center justify-center mr-5 text-lighttxt transition-all duration-300 ease-linear;
  }
  .collapsedtab-icon {
    @apply relative flex items-center justify-center text-lighttxt transition-all duration-300 ease-linear;
  }
  .tab-text {
    @apply font-semibold text-xl text-lighttxt align-text-bottom transition-all duration-300 ease-linear;
  }
  .tab-item {
    @apply relative flex p-5 rounded-2xl mb-2 transition-all duration-300 ease-linear hover:bg-backlight;
  }
  .tab-tooltip {
    @apply absolute w-auto p-2 left-24 rounded-md min-w-max bg-gray-400 text-xs font-semibold transition-all duration-200 scale-0 origin-left z-10;
  }
}
